<template>
  <div class="row mb-2">
    <div id="home" class="col-sm-12 text-center">
      <div class="background">
        <h1>pizza!</h1>
        <h2>pizza!</h2>
        <button class="btn btn-success" @click="gotoMenu">go to Order!</button>
      </div>
    </div>
  </div>
</template>


<script>
export default {
  methods: {
    gotoMenu() {
      // this.$router.go(-1)
      // this.$router.replace('/login')
      this.$router.push({ name: "loginLink" });
    }
  }
};
</script>

<style scoped>
    #home{
        background:url('../assets/bg.jpg') no-repeat center center/contain;
        height: 85vh;
  

        padding: 10%;
    }
    .background{
        background:rgba(255,255,255,.7);
        max-width: 70vw;
        margin:  0 auto;
        padding: 20px 0;
    }
    h1,h2{
        margin: 6%;
    }
</style>




