import Home from "./components/Home";
import Menu from "./components/Menu";
import Admin from "./components/Admin";
import About from "./components/about/About";
import Register from "./components/Register";
import Login from "./components/Login";
//
import Contact from "./components/about/Contact.vue";
import Delivery from "./components/about/Delivery.vue";
import History from "./components/about/History.vue";
import OrderingGuide from "./components/about/OrderingGuide.vue";

//
import PersonName from "./components/about/contact/PersonName.vue";
import PersonPhone from "./components/about/contact/PersonPhone.vue";

export const routes = [
  { path: "/", name: "homeLink", components:{
    default:Home,
    'ppp3':OrderingGuide,
    'ppp4':Delivery,
    'ppp5':History
  }},
  { path: "/menu", name: "menuLink", component: Menu },
  {
    path: "/admin",
    name: "adminLink",
    component: Admin,
   
  },

  {
    path: "/about",
    name: "aboutLink",
    component: About,
    children: [
      {
        path: "/about/contact",
        name: "contactLink",
        component: Contact,
        children: [
          {
            path: "/about/contact/personName",
            name: "personNameLink",
            component: PersonName
          },
          {
            path: "/about/contact/personPhone",
            name: "personPhoneLink",
            component: PersonPhone
          }
        ]
      },
      { path: "/about/delivery", name: "deliveryLink", component: Delivery },
      { path: "/about/hisroty", name: "hisrotyLink", component: History },
      {
        path: "/about/orderingGuide",
        name: "orderingGuideLink",
        component: OrderingGuide
      }
    ],
    redirect: "/about/orderingGuide"
  },

  { path: "/register", name: "registerLink", component: Register },
  { path: "/login", name: "loginLink", component: Login },
  { path: "*", redirect: "/" }
];



